import React from "react";
import image1 from "./image/IMAGE1.png";
import image2 from "./image/IMAGE2.png";
import image3 from "./image/IMAGE3.jpeg";
import image4 from "./image/IMAGE4.png";
import image5 from "./image/IMAGE5.png";
import image6 from "./image/IMAGE6.png";
import image7 from "./image/IMAGE7.png";
import image8 from "./image/IMAGE8.png";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Link } from "react-router-dom";

export function Home() {
  return (
    <>
      {/* Navigation */}
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container px-4 px-lg-5">
          <a className="navbar-brand" href="/shoes.github.io">
            LKH Shoes Store
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ms-lg-4">
              <li className="nav-item">
                <a
                  className="nav-link active"
                  aria-current="page"
                  href="/shoes.github.io"
                >
                  Home
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#!">
                  Recommend
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="https://yoyo0527.github.io/form.github.io/comment.html">
                Customer Feedback                
                </a>
              </li>
              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  id="navbarDropdown"
                  href="#"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Shop
                </a>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <a className="dropdown-item" href="#!">
                      All Products
                    </a>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <a className="dropdown-item" href="#!">
                      Popular Items
                    </a>
                  </li>
                </ul>
              </li>
            </ul>
            <form className="d-flex">
              <Link to="login">
                <button className="btn btn-outline-dark">
                  <i className="bi bi-person-fill"></i>
                  Log in
                </button>
              </Link>
            </form>
          </div>
        </div>
      </nav>
      {/* Header */}
      <header className="bg-dark py-5">
        <Carousel
          autoPlay={true}
          interval={3000}
          infiniteLoop={true}
          showThumbs={false}
        >
          <div>
            <img src={image1} alt="Image 1" />
          </div>
          <div>
            <img src={image2} alt="Image 2" />
          </div>
          <div>
            <img src={image3} alt="Image 3" />
          </div>
        </Carousel>
      </header>
      {/* Section */}
      <section className="py-5">
        <div className="container px-4 px-lg-5 mt-5">
          <h1>LKH's pick</h1>
          <div className="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
            
            <div className="col mb-5">
              <div className="card h-100">
                {/* Product image */}
                <img className="card-img-top" src={image4} alt="..." />
                {/* Product details */}
                <div className="card-body p-4">
                  <div className="text-center">
                    {/* Product name */}
                    <h5 className="fw-bolder">JA 1 DAY ONE COBALT BLISS</h5>
                    {/* Product price */}
                    NT$ 3,982 +
                  </div>
                </div>
                {/* Product actions */}
                <div className="card-footer p-4 pt-0 border-top-0 bg-transparent text-center">
                  <div className="d-grid">
                    <button className="btn btn-outline-dark" type="button" onClick={hint}>
                      <i className="bi-cart-fill me-1"></i>
                      Add to cart
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="col mb-5">
              <div className="card h-100">
                {/* Product image */}
                <img className="card-img-top" src={image5} alt="..." />
                {/* Product details */}
                <div className="card-body p-4">
                  <div className="text-center">
                    {/* Product name */}
                    <h5 className="fw-bolder">YEEZY FOAM RUNNER MX CINDER</h5>
                    {/* Product price */}
                    NT$ 4,694 +
                  </div>
                </div>
                {/* Product actions */}
                <div className="card-footer p-4 pt-0 border-top-0 bg-transparent text-center">
                  <div className="d-grid">
                    <button className="btn btn-outline-dark" type="button" onClick={hint}>
                      <i className="bi-cart-fill me-1"></i>
                      Add to cart
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="col mb-5">
              <div className="card h-100">
                {/* Product image */}
                <img className="card-img-top" src={image6} alt="..." />
                {/* Product details */}
                <div className="card-body p-4">
                  <div className="text-center">
                    {/* Product name */}
                    <h5 className="fw-bolder">DUNK LOW RETRO WHITE BLACK</h5>
                    {/* Product price */}
                    NT$ 3,864 +
                  </div>
                </div>
                {/* Product actions */}
                <div className="card-footer p-4 pt-0 border-top-0 bg-transparent text-center">
                  <div className="d-grid">
                    <button className="btn btn-outline-dark" type="button" onClick={hint}>
                      <i className="bi-cart-fill me-1"></i>
                      Add to cart
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="col mb-5">
              <div className="card h-100">
                {/* Product image */}
                <img className="card-img-top" src={image7} alt="..." />
                {/* Product details */}
                <div className="card-body p-4">
                  <div className="text-center">
                    {/* Product name */}
                    <h5 className="fw-bolder">SAMBA OG WHITE BLACK</h5>
                    {/* Product price */}
                    NT$ 3,843 +
                  </div>
                </div>
                {/* Product actions */}
                <div className="card-footer p-4 pt-0 border-top-0 bg-transparent text-center">
                  <div className="d-grid">
                    <button className="btn btn-outline-dark" type="button" onClick={hint}>
                      <i className="bi-cart-fill me-1"></i>
                      Add to cart
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="col mb-5">
              <div className="card h-100">
                {/* Product image */}
                <img className="card-img-top" src={image8} alt="..." />
                {/* Product details */}
                <div className="card-body p-4">
                  <div className="text-center">
                    {/* Product name */}
                    <h5 className="fw-bolder">MB.02 LAMELO BALL LAMELO'S GREEN</h5>
                    {/* Product price */}
                    NT$ 7,099 +  
                  </div>
                </div>
                {/* Product actions */}
                <div className="card-footer p-4 pt-0 border-top-0 bg-transparent text-center">
                  <div className="d-grid">
                    <button className="btn btn-outline-dark" type="button" onClick={hint}>
                      <i className="bi-cart-fill me-1"></i>
                      Add to cart
                    </button>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </div>
      </section>
    </>
  );
}

function hint(){
    alert("")
}