import './App.css';
import { Route, Routes } from 'react-router-dom';
import { Show } from './Showpage';
import { Loginpage } from './loginpage';
import { Home } from './Homepage';

function App() {
  return (
    <div className="App">
      <Routes>
      <Route path='/shoes.github.io/' element={<Home/>}></Route>
        <Route path='/shoes.github.io/show/:username' element={<Show/>}></Route>
        <Route path='/shoes.github.io/login' element={<Loginpage/>}></Route>
      </Routes>
    </div>
  );
}

export default App;
