import React from "react";
import image7 from "./image/image7.webp";
import { useNavigate } from "react-router-dom";

export function Loginpage() {
  const handleLogin = (event) => {
    event.preventDefault();

    const account = document.getElementById("account").value;
    const password = document.getElementById("password").value;

    Readsheet(account,password); //  Readsheet  email 
  };
  const navigate = useNavigate();
  const toshowpage = (username) => {
    navigate(`/shoes.github.io/show/${username}`);
  };
  function Readsheet(account,password) {
    fetch(`https://sheetdb.io/api/v1/oevvd1fhl7edq/search?account=${account}&password=${password}`)
      .then((response) => response.json())
      .then((data) => {
        let res = data.length;
        if (res === 0) {
          alert("")
        }
        else{
          alert("")
          toshowpage(data[0].username);
        }
        console.log(res);
      });
  }
  return (
    <>
      <div className="container">
        {/* <!-- Outer Row --> */}
        <div className="row justify-content-center">
          <div className="col-xl-10 col-lg-12 col-md-9">
            <div className="card o-hidden border-0 shadow-lg my-5">
              <div className="card-body p-0">
                {/* <!-- Nested Row within Card Body --> */}
                <div className="row">
                  <div className="col-lg-6 d-none d-lg-block">
                    <img src={image7} style={{ width: "100%" }}></img>
                  </div>
                  <div className="col-lg-6">
                    <div className="p-5">
                      <div className="text-center">
                        <h1 className="h4 text-gray-900 mb-4">Welcome Back!</h1>
                      </div>
                      <form className="user" onSubmit={handleLogin}>
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control form-control-user"
                            id="account"
                            aria-describedby="emailHelp"
                            placeholder="Enter account..."
                          />
                        </div>
                        <div className="form-group">
                          <input
                            type="password"
                            className="form-control form-control-user"
                            id="password"
                            placeholder="Password"
                          />
                        </div>
                        <a
                          className="btn btn-primary btn-user btn-block"
                          onClick={handleLogin}
                        >
                          Login
                        </a>
                      </form>
                      <hr />
                      <div className="text-center">
                        <a
                          className="small"
                          href="https://yoyo0527.github.io/form.github.io/form.html"
                        >
                          Create an Account!
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

